@import './vars';

.toggle-btn-section {
    position: absolute;
    top: 1vmax;
    align-self: flex-end;
    display: flex;
    place-items: center;
    border: 1px solid;
    border-radius: 1.5vmax;
    border-color: #25284f;

    img {
        margin: auto 4px;
        width: 2vmax;
    }

    .toggle-checkbox {
        justify-content: center;
        display: inline-flex;
        align-items: center;
        cursor: pointer;

        .toggle-btn__input {
            height: 0;
            width: 0;
            visibility: hidden;
        }

        .toggle-btn__input-label {
            cursor: pointer;
            text-indent: -9999px;
            width: 3vmax;
            height: 1.25vmax;
            display: block;
            border-radius: 23px;
            position: relative;
            border: none;
            margin-top: 2px;

            &::after {
                content: ' ';
                position: absolute;
                top: 0.1vmax;
                left: 3px;
                width: 1vmax;
                height: 1vmax;
                border-radius: 50%;
                transition: 0.3s;
                box-shadow: 0 5px 10px rgba(153, 153, 153, 0.4);
                background-color: #fff;
            }
        }

        .toggle-btn__input:checked~.toggle-btn__input-label {
            &:after {
                background: #fff;
                left: calc(100% - 3px);
                transform: translateX(-100%);
            }
        }

        .toggle-btn__input:checked~.toggle-btn__theme {
            &:after {
                // theme switch circle background in light theme
                background: #fff;
                left: calc(100% - 3px);
                transform: translateX(-100%);
            }
        }
    }

    .toggle-btn__input:checked~.toggle-btn__input-label {
        background-color: #25284f;
    }
}